<template >
  <div >
    <v-card hover dark class="elevation-18" >
      <v-img
        :src="anime.anime_img"
        aspect-ratio="0.8"
        class="grey lighten-2"
        @click="pushroute('/subbed/'+ anime.anime_id + '/null')"
      >
        <v-layout
          slot="placeholder"
          fill-height
          align-center
          justify-center
          ma-0
        >
          <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
        </v-layout>
      </v-img>
      <v-card-actions>
        {{anime.anime_name}}
        <v-spacer></v-spacer>
        <v-menu
          bottom
          origin="center center"
          transition="scale-transition"
        >
          <v-btn
            slot="activator"
            icon
          >
            <v-icon>more_vert</v-icon>
          </v-btn>
          <v-list>
            <v-list-tile @click="add_list=true">
              <v-list-tile-title>Add to My list</v-list-tile-title>
            </v-list-tile>
            <v-list-tile @click="fb_share('/subbed/'+ anime.anime_id + '/null');">
              <v-list-tile-title>share</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
      </v-card-actions>
    </v-card>
    <v-dialog
        v-model="add_list"
        width="500"
      >
        <v-card>
          <v-card-title
          class="secondary white--text headline"
            primary-title
          >
            Add to My List
          </v-card-title>
          <div style="height:200px;">
            <v-list v-for="(n,index) in list" :key="index">

              <v-list-tile @click="addtoList(n.list_id, anime.anime_id);add_list=false;" >

                <v-list-tile-avatar  >
                  <v-icon class="pl-4">playlist_play</v-icon>
                </v-list-tile-avatar>

                <v-list-tile-content >
                  <v-list-tile-title> {{n.list_name1}}</v-list-tile-title>
                </v-list-tile-content>


              </v-list-tile>
            </v-list>
          </div>
        </v-card>
      </v-dialog>
    <v-dialog v-model="dialog"  fullscreen transition="dialog-bottom-transition">
      <v-card style="width:100%;height:100%">
        <v-toolbar dark color="primary" height="50px">
              <v-toolbar-title>{{anime.anime_name}}</v-toolbar-title>
              <v-spacer></v-spacer>
              <v-toolbar-items>
                <v-btn icon @click.native="fb_share('/'+anime.anime_id);dialog = false"><v-icon>share</v-icon></v-btn>
                <v-btn icon @click.native="dialog = false">
                  <v-icon>close</v-icon>
                </v-btn>
              </v-toolbar-items>
          </v-toolbar>
    </v-card>
    </v-dialog>
  </div>

</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  computed:{
    list(){
      return this.$store.getters.getList;
    },
    profile(){
      return this.$store.getters.getProfile;
    }
  },
  props:{
    anime:{}
  },
  components:{
  },
  data () {
      return {
        dialog:false,
        add_list : false,
      }
  },
  methods:{
    addtoList:function(list, anime){
      this.$http.post(this.host+ '/api/Playlists',{
        list_id:list,
        anime_id:anime,
        user_id:this.profile.user_id,
      });
    },
    fb_share:function(content){
      FB.ui({
        method: 'share',
        mobile_iframe: true,
        href: this.host + content,
      }, function(response){});
    },

  }
}
</script>

<style lang="css">
</style>
