<template>
  <v-card height='100%'>
    <v-parallax :src="require('../../assets/img/para1.jpg')" height="340" class="area">
      <ul class="circles">
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
      </ul>
    </v-parallax>
    <v-layout
      column
      align-center
    >
      <img
      class="img elevation-4"
      style="position: absolute;top: 170px;z-index: 1;"
      :src='team.team_img' height="230" width="230" >
      <div style="position: absolute;top:110px;z-index: 1;" >
        <span class="font-weight-black accent--text display-2" style="text-shadow:5px 5px 10px black;-webkit-text-stroke: 1px white;">{{team.team_name}}</span></br>
      </div>
      <div style="position: absolute;top:407px;z-index: 1;">
        <span class="font-weight-bold grey--text text-xs-center ">@ass is better than boobs</span>
      </div>
    </v-layout>
    <div style="padding:45px"></div>
    <div class="text-xs-center" style="width:100%">
      <div><v-btn outline class="accent" @click="pushroute('/projects/'+ myteam.team_id)">
        <v-icon class="accent--text" >
          folder_open
        </v-icon><span class="accent--text">&nbspProjects</span></v-btn></div>
    </div>
    <v-layout>
      <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex>
      <v-flex xs12 sm12 md8 lg8>
        <div style="paddingTop:30px;">
            <p class="fancy font-weight-black accent--text display-1" style="-webkit-text-stroke: 1px light-green;"><span>Members</span></p>
        </div>
        <v-layout wrap justify-center>
          <v-flex xs12 sm4 md4 lg2 v-for='(item,index) in members' :key='index' >
            <v-card class="ma-2" style="border-radius:5px">
              <v-layout
                column
                align-center
                pa-2
              >
              <v-avatar
                 size="100"
               >
                 <img  class="elevation-5 profile" :src='item.user_avatar' alt="avatar">
              </v-avatar>
             </v-layout>
             <v-divider></v-divider>
             <v-card-title>
                <div class="text-xs-center" style="width:100%">
                  <div class="text-xs-center headline mb-0">{{item.user_name}}</div>
                  <div > Admin </div>
                </div>
              </v-card-title>
              <!-- <v-divider></v-divider> -->
              <!-- <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn flat color="orange">Share</v-btn>
                <v-btn flat color="orange">Explore</v-btn>
              </v-card-actions> -->
            </v-card>
          </v-flex>
          <v-flex xs12 sm4 md4 lg2 v-show="checkadmin()">
            <div style="height:100%;" class="pr-3 pb-3 pt-1" >
              <v-btn style="width:100%;height:100%;border-radius:5px;" class="accent"><v-icon size="150">add</v-icon></v-btn>
            </div>
          </v-flex>
        </v-layout>
        <div style="paddingTop:30px;">
            <p class="fancy font-weight-black accent--text display-1" style="-webkit-text-stroke: 1px grey;"><span>About</span></p>
            <p class="text-lg-center pr-2 pl-2">{{team.team_description}}</p>
        </div>
      </v-flex>
      <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex>
    </v-layout>
  </v-card>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  data(){
    return{
      team:[],
      members:[],
    }
  },
  computed:{
    myteam(){
      return this.$store.getters.getteam;
    }
  },
  methods:{
    checkadmin(){
      if(this.myteam.team_id!=this.$route.params.team_id){
        return false;
      }else{
        return true;
      }
    },
    getinfo(){
      var _this=this;
      this.$http.get(this.host + '/api/Teams/' + this.$route.params.team_id).then(function(data){
        _this.team=data.body;
        _this.team.team_img=_this.host + '/api/Team/Img/Get/' + this.$route.params.team_id;
      });

      this.$http.get(this.host + '/api/Teams/Members/' + this.$route.params.team_id).then(function(data){
        _this.members=data.body;
      });
    },
  },
  watch:{
  $route (to, from){
    this.getinfo();
   }
 },
  created(){
    this.getinfo();
  },
}
</script>

<style scoped>
.img {
  border: 0px solid #ddd;
  border-radius: 10px;
  padding: 4px;
  background: linear-gradient(to right bottom, #f932fc 0%, #bc26bf 10%, #7db9e8 41%, #85f78f 61%, #eff484 81%, #ff8c8e 100%);
}

.profile{
  border: 0px solid #ddd;
  border-radius: 100px;
  padding: 4px;
}
.fancy {
  line-height: 0.5;
  text-align: center;
}
.fancy span {
  display: inline-block;
  position: relative;
}
.fancy span:before,
.fancy span:after {
  content: '';
  position: absolute;
  height: 1px;
  border-bottom: 1px solid grey;
  border-top: 1px solid grey;
  top: 18px;
  width: 25vw;
}
.fancy span:before {
  right: 100%;
  margin-right: 15px;
}
.fancy span:after {
  left: 100%;
  margin-left: 15px;
}
</style>
