<template>
  <v-content>
  <v-container fluid grid-list-sm class="pa-0">
    <v-layout row wrap>
      <v-flex hidden-xs-sm-only md1 lg1>

      </v-flex>
      <v-flex xs12 sm12 md10 lg10 >
        <v-layout row wrap >
          <v-flex d-flex xs12 sm12 md12 lg12 class="pa-2">
            <v-card width='100%'>
            <v-layout row wrap>
              <v-flex xs12 sm2 md2 lg2>
                <v-img
                  :src='team.team_img'
                  aspect-ratio="1"
                  class="grey elevation-4"
                >
                  <v-layout
                    slot="placeholder"
                    fill-height
                    align-center
                    justify-center
                    ma-0
                  >
                    <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                  </v-layout>
                </v-img>
              </v-flex>
              <v-flex xs12 sm10 md10 lg10 >
                <v-card height="100%" class="elevation-4">
                    <v-card-text><span class="font-weight-bold accent--text headline">{{this.team.team_name}}</span></v-card-text>
                    <v-divider></v-divider>
                    <v-card-text><span class="accent--text "></span><span>{{this.team.team_description}}</span></v-card-text>
                </v-card>
              </v-flex>
            </v-layout>
            </v-card>
          </v-flex>

          <v-flex xs12 sm12 md12 lg12 class="pl-4 pr-4 pt-0">
            <v-tabs

              dark
              icons-and-text
              grow
              height='55'
            >
              <v-tabs-slider color="accent"></v-tabs-slider>

              <v-tab href="#tab-1">
                Projects
                <v-icon>folder_open</v-icon>
              </v-tab>

              <v-tab href="#tab-2">
                members
                <v-icon>people</v-icon>
              </v-tab>

              <v-tabs-items
                :touchless="true"
              >
              <v-tab-item
                v-for="i in 2"
                :value="'tab-' + i"
                :key="i"
              >

                <div v-if='i===1'>
                  <v-card >
                    <v-card-actions>
                      <v-select
                      :items="['All', 'Completed', 'Ongoing']"
                      hide-details
                      v-model="selected"
                      class="pr-2 pt-2"
                      style="width:0px;"
                      >
                      </v-select>
                      <v-text-field
                      style="width:200px"
                        v-model="searchtable"
                        append-icon="search"
                        label="Search"
                        single-line
                        hide-details
                        width=100
                        color="accent"
                      ></v-text-field>
                      <v-spacer></v-spacer>
                        <div v-if="checkadmin">
                          <v-btn fab small class="mr-2 mt-1 accent" @click="create=false;addproject=true;createnewproject();">
                            <v-icon>add</v-icon>
                          </v-btn>
                        </div>
                      <!-- </v-btn-toggle> -->
                      <v-dialog width=80% persistent v-model="addproject">
                        <v-card>
                          <create-project :project_id='project_id' :addtoproject='create'></create-project>
                        </v-card>
                        <v-card dark>
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn flat @click='create=false;addproject=false;cancelcreate();'>Cancel</v-btn>
                            <v-btn color="secondary" @click='create=true;addproject=false;'>Add</v-btn>
                            </v-card-actions>
                        </v-card>
                        </v-dialog>
                    </v-card-actions>
                    <v-data-table
                      :headers="project_headers"
                      :items="filter_table"
                      :search="searchtable"
                    >
                      <template slot="items" slot-scope="props">
                          <tr @click="pushroute('/projects/' + props.item.project_id+ '/' + props.item.anime_id)">
                          <td class="pl-2 pt-2" width="200"><img :src='props.item.anime_img' width="160px"></td>
                          <td class="text-xs-left"  >{{ props.item.anime_name }}</td>
                          <td class="text-xs-left" >{{ props.item.anime_type }}</td>
                          <td class="text-xs-left" >{{ props.item.anime_episodes }}</td>
                          <td class="text-xs-left" >{{ props.item.finished_episodes }}</td>
                          <td class="text-xs-right">
                              <v-progress-circular
                                :rotate="-90"
                                :size="100"
                                :width="15"
                                :value='(props.item.finished_episodes/props.item.anime_episodes)*100'
                                color="accent"
                              >
                                {{Math.round((props.item.finished_episodes/props.item.anime_episodes)*100)}} %
                              </v-progress-circular>
                          </td>
                        </tr>
                      </template>

                      <v-alert slot="no-results" :value="true" color="error" icon="warning">
                        Your search for "{{ searchtable }}" found no results.
                      </v-alert>
                    </v-data-table>
                  </v-card>
                </div>

                <div v-if='i===2'>
                  <v-card>
                    <v-card-title class="pa-1 pl-2">
                      <v-spacer></v-spacer>
                      <div v-if="checkadmin">
                      <v-dialog  max-width="500px">
                        <v-btn class="mr-2 accent"fab small slot="activator">
                          <v-icon>add</v-icon>
                        </v-btn>
                        <v-card>
                        <!-- <invite-member :createNew='false'></invite-member> -->
                        <!-- invite -->
                        <div >
                          <v-autocomplete
                            pa-1
                            v-model="modelm"
                            :items="invites"
                            :loading="isLoading"
                            :search-input.sync="search"
                            hide-details
                            hide-selected
                            item-text="user_name"
                            item-value="user_id"
                            label="Search members to invite....."
                            solo
                            clearable
                            background-color="secondary lighten-1"
                            dark
                            @change='member_invited(modelm);'
                          >
                            <template slot="no-data">
                              <v-list-tile>
                                <v-list-tile-title>
                                  Search for
                                  <strong>People</strong>
                                </v-list-tile-title>
                              </v-list-tile>
                            </template>

                            <template
                              slot="item"
                              slot-scope="{ item, tile }"

                            >
                              <v-list-tile-avatar
                                color="indigo"
                                class="headline font-weight-light white--text"
                              >
                                <img :src='item.user_avatar' alt="avatar" width="100">
                              </v-list-tile-avatar>

                              <v-list-tile-content>
                                <v-list-tile-title v-text='item.user_name'></v-list-tile-title>
                                <v-list-tile-sub-title v-text="item.user_id"></v-list-tile-sub-title>
                              </v-list-tile-content>
                              <v-list-tile-action>
                                <v-icon>mdi-coin</v-icon>
                              </v-list-tile-action>
                            </template>
                          </v-autocomplete>
                        </div>
                        <!-- invite -->

                      </v-card>
                      </v-dialog>
                    </div>
                   </v-card-title>
                   <v-divider></v-divider>
                          <v-data-table
                            :headers="headers"
                            :items="members"
                            hide-actions
                            class="elevation-1"
                          >
                            <template slot="items" slot-scope="props">
                              <td class="pt-3 pb-3"> <v-avatar  size="60"><img :src='props.item.user_avatar'></v-avatar></td>
                              <td class="text-xs-left" >{{ props.item.user_name }}</td>
                              <td class="text-xs-left" v-if='props.item.team_role==null'>Member</td>
                              <td class="text-xs-left" v-else>{{props.item.team_role.toUpperCase()}}</td>
                            </template>
                          </v-data-table>
                    </v-card>
                </div>

              </v-tab-item>
            </v-tabs-items>
            </v-tabs>


          </v-flex>
          <v-flex hidden-xs-sm-only md1 lg1>

          </v-flex>
        </v-layout>
    </v-flex>
    <v-flex xs1 sm1 md1 lg1>

  </v-flex>
  <v-card>
    <v-snackbar
      v-model="snackbar"
      color="success"
      :timeout="6000"
    >
      Member Invited ! Please wait for them to accept your request.
      <v-btn
        dark
        flat
        @click="snackbar = false"
      >
        Close
      </v-btn>
    </v-snackbar>
  </v-card>

</v-layout>
</v-container>
</v-content>
</template>

<script>
import create from './create_dialog.vue'
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  components:{
    'create-project': create
  },
  computed:{
    profile(){
      return this.$store.getters.getProfile;
    },
    checkadmin(){
      if(this.profile.team_role===null || this.profile.team_role==='' || this.profile.team_role.length>5){
        return false;
      }else{
        return true;
      }
    },
    filter_table(){
      if(this.selected=='All'){
        return this.projects;
      }else if(this.selected=='Completed'){
        return this.projects.filter((i)=>{
          return i.anime_episodes==i.finished_episodes;
        });
      }else{
        return this.projects.filter((i)=>{
          return i.anime_episodes>i.finished_episodes;
        });
      }
    }
  },
  created(){
    var _this=this;
    this.$http.get(this.host + '/api/Teams/' + this.$route.params.id).then(function(data){
      _this.team=data.body;
      _this.team.team_img=_this.host + '/api/Team/Img/Get/' + this.$route.params.id;
    });

    this.$http.get(this.host + '/api/Teams/Members/' + this.$route.params.id).then(function(data){
      _this.members=data.body;
    });

    this.$http.get(this.host+'/api/Project/TeamProject/' + this.$route.params.id).then(function(data){
      _this.projects=data.body;
    });
  },
  methods:{
    member_invited:function(id){
      if(id===null || id.length<=0 || id===''){
      }else {
        this.$http.post(this.host+ '/api/Invites/',{
          invite_id:'',
          user_id:id,
          team_id:this.team.team_id
        }).then(response => {
          this.snackbar=true;
          this.modelm='';
        }, response => {

        });

      }
    },
    cancelcreate:function(){
      this.$http.delete(this.host + '/api/Projects/DeleteNull').then(function(data){
      });
    },
    convertNull:function(val){
      if(val===null){
        return 0
      }
    },
    createnewproject(){
      this.$http.post(this.host + '/api/Projects/', {team_id:this.team.team_id, finished_episodes: 0}).then(response => {
        this.project_id=response.body.project_id;
      }, response => {

      });
    },
  },
  data(){
    return{
      team:[],
      projects:[],
      members:[],
      selected:'All',
      create:false,
      snackbar:false,
      modelm:'',
      invites:[],
      isLoading:false,
      search:'',
      project_id:'',
      addproject:false,
      dialog:false,
      tab: null,
        items: [
          'Projects', 'Members'
        ],
      searchtable: '',
      headers:[
          {
            text: 'Profile',
            align: 'left',
            sortable: false,
            value: 'profile'
          },
          { text: 'Name', value: 'name', align:'left' },
          { text: 'Role', value: 'role', align:'left' },
        ],
        project_headers:[
            {
              text: 'Image',
              align: 'left',
              sortable: false,
              value: 'img'
            },
            { text: 'Name', value: 'name', align:'left' },
            { text: 'Type', value: 'type', align:'left' },
            { text: 'Episodes', value: 'epi', align:'left' },
            { text: 'Episodes finished', value: 'epi', align:'left' },
            { text: 'Over All Progress', value: 'prog', align: 'right'}
        ]
    }
  },
  watch:{
    search (val) {
      try {
        if(val!==null){
          if(val.length>=0){
            this.isLoading=true;
            var _this=this;
            this.$http.get(this.host+'/api/User/Invite/'+ val).then(data => {
              _this.invites=data.body;
              _this.isLoading=false
            }, data => {

            });
          }

        }
      }
      catch(err) {
        console.log(err);
        // this.items=[];
      }
    }
  }
}
</script>

<style>
</style>
