<template>
  <v-toolbar
    dark
    color="primary"
    dense
    fixed
    app
    height="60px"
  >
    <v-toolbar-title class="align-center title" >
      <span v-on:click="goPush('/')">ASTRO</span>

    </v-toolbar-title>

    <v-spacer></v-spacer>
    <v-toolbar-items >
      <v-btn icon v-show='!full' @click="full=true;goPush('/search')">
      <v-icon >search</v-icon>
    </v-btn>
      <v-text-field
        ref="input"
        v-show='full'
        v-model='keyword'
        class="pt-2"
        flat
        label="Search"
        prepend-inner-icon="search"
        solo-inverted
        @blur="full=false"
        v-on:keypress.enter="searchKey"
      ></v-text-field>
      <!-- <div v-if='this.profile.user_id!==null'> -->
        <v-menu
          :close-on-content-click="false"
          :nudge-width="200"
          offset-x
          v-show='this.shownoti'
        >
          <v-btn icon slot="activator" v-show='this.profile.user_id!==null'>
          <v-badge>
            <span slot="badge" class="red--text">{{showNoti}}</span>
            <v-icon >
              notifications
            </v-icon>
            </v-badge>
          </v-btn>
          <app-noti :notification='notis' :invites='invites'></app-noti>
        </v-menu>
      <!-- </div> -->

          <v-btn
              v-on:click="checkLogin()"
              slot="activator"
              icon
          >
            <div v-if='this.profile.user_id===null'>
                <v-icon>input</v-icon>
            </div>
            <div v-else>
              <v-icon>
                menu
              </v-icon>
            </div>
          </v-btn>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
import {bus} from '../../main';
import noti from './Notification.vue';
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  components:{
    'app-noti' : noti
  },
  computed:{
    showNoti(){
      var count=0;
        for (var i = 0; i < this.invites.length; i++) {
            count=count+1;
        }
      for (var i = 0; i < this.notis.length; i++) {
        if(this.notis[i].seen===false){
          count=count+1;
        }
      }
      if(count<=0){
        this.shownoti=false;
      }else{
        this.shownoti=true;
        return count;
      }
    },
    profile(){
      return this.$store.getters.getProfile;
    },
    team(){
      return this.$store.getters.getProfile.team_name;
    },
    login_status(){
      return this.$store.getters.loginStatus;
    },
  },
  watch:{
    full(val){
      if(val===true){
       var temp =this
        setTimeout(function(){
          temp.$nextTick(temp.$refs.input.focus());
        }, 100)
      }
    }
  },
  data(){
    return{
      shownoti:false,
      invites:[],
      keyword:'',
      location:{},
      full:false,
      notis:[]
    }
  },
  created(){
    if(this.profile.user_id!==null){
      this.getnoti(this.profile.user_id);
    }
  },
  methods:{
    getinvites:function(id){
      this.$http.get(this.host+'/api/Notis/'+id).then(response => {
          this.notis=response.body;
      }, response => {
      });
    },
    getprojectnoti:function(id){
      this.$http.get(this.host+'/api/Invites/'+id).then(response => {
          this.invites=response.body;
        }, response => {
        });
    },
    getnoti:function(id){
      var _this=this;
      setInterval(function(){
        _this.getinvites(id);
        _this.getprojectnoti(id);
      }, 2000);
    },
    searchKey:function(){
      // bus.$emit('searchKeyEntered',this.keyword);
      this.$store.dispatch('toggle_Loading');
      this.$http.get(this.host+'/api/Anime?keyword='+ this.keyword +'').then(function(data){
      this.$store.dispatch('setAnime',data.body);
      });

    },
    goPush:function(path){
      this.$router.push(path);
    },
    checkLogin:function(){
        if(this.login_status===false){
          this.login_app();
        }else{
          bus.$emit('toggleappdrawer');
        }
      },

    login_app:function(){
      var _store=this
      FB.login(function(response) {
        if (response.status === 'connected') {
          // console.log(response);
          console.log(response.status);
          console.log("Logged in");
          FB.api('/me', profile => {
            var person = {user_id:profile.id, user_name:profile.name, user_avatar:"https://graph.facebook.com/" + profile.id + "/picture?type=normal", team_role:null};
            _store.$store.dispatch('setProfile',person);
            _store.getuserinfo(person);
          });
            _store.$store.dispatch('toggle_Login');
            _store.getlocation();

        } else {
          console.log("not logged in");
        }
      },{scope: 'public_profile,email'});
      console.log(this.location);
    },
    getlocation:function(){
      if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(this.setLocation);
      };
    },


    setLocation:function(location){
      this.location=location;
      this.$http.get('https://us1.locationiq.com/v1/reverse.php?key=84302eaf26a66d&lat='+ this.location.coords.latitude + '&lon='+ this.location.coords.longitude + '&format=json').then(function(data){
      this.$store.dispatch('setCountry',data.body.address.country);
      });
    }
  },
}
</script>

<style scoped>
span:hover{
   cursor: pointer;
}

</style>
