export default{
  data(){
    return {
      host : "http://localhost:1025"
    }
  },
  methods:{
    getuserinfo:function(person){
      var _this=this;
      this.$http.post(this.host+'/api/User',{
        user_id:Number(person.user_id),
        user_name: person.user_name,
        user_avatar: person.user_avatar
      }).then(function(data){
        // this.$store.dispatch('setProfile',{user_id:data.body.user_id,user_name:data.body.user_name,user_avatar:data.body.user_avatar,team_role:data.body.team_role});
        if(data.statusText==="OK"){
          _this.$store.dispatch('setProfile',{user_id:data.body.user_id,user_name:data.body.user_name,user_avatar:data.body.user_avatar,team_role:data.body.team_role})
          var team=[];
          team.team_id=data.body.team_id;
          team.team_name=data.body.team_name;
          team.team_img=this.host+'/api/Team/Img/Get/' + data.body.team_id;
          team.team_description=data.body.team_description;
          this.$store.dispatch('setTeam',team);
          // this.$store.dispatch('setRole');
        }
        this.getplaylist(person.user_id);
      });
    },
    getplaylist:function(id){
      this.$http.get(this.host+'/api/List/'+id).then(response => {
        this.$store.dispatch('setList',response.body);
      }, response => {
        this.$store.state.playlist=[];
      });
    },

    pushroute:function(route){
      this.$router.push(route)
    },
  }
}
