<template>
  <div >
    <v-alert
      :value=true
      color="info"
      icon="info"
      outline
      dismissible
    >
      Invite people to your team. It is optional but a standard translation team should contain at least three or four people. You can also add members later.
      Note : People who only logged in into this website will be shown in search result.
    </v-alert>
    <v-autocomplete
      pa-1
      v-model="model"
      :items="items"
      :loading="isLoading"
      :search-input.sync="search"
      clearable
      hide-details
      hide-selected
      item-text="user_name"
      item-value="user_id"
      label="Search members to invite....."
      solo
      hide-no-data
      background-color="secondary lighten-1"
      dark
    >

      <template
        slot="item"
        slot-scope="{ item, tile }"
      >
        <v-list-tile-avatar
          color="indigo"
          class="headline font-weight-light white--text"
        >
          <img :src='item.user_avatar' alt="avatar" width="100">
        </v-list-tile-avatar>

        <v-list-tile-content>
          <v-list-tile-title v-text='item.user_name'></v-list-tile-title>
          <v-list-tile-sub-title v-text="item.user_id"></v-list-tile-sub-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <v-icon>mdi-coin</v-icon>
        </v-list-tile-action>
      </template>
    </v-autocomplete>

    <v-list subheader class="pa-2">
          <div
            v-for="item in invited"
            :key="item.user_id">
            <v-list-tile
              avatar
              @click=""
            >

            <v-list-tile-avatar
              color="indigo"
              class="headline font-weight-light white--text"
            >
              <img :src='item.user_avatar' alt="avatar" width="100">
            </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title v-html="item.user_name"></v-list-tile-title>
              </v-list-tile-content>

              <v-list-tile-action>
                <v-btn fab small color="red lighten-1" @click="remove_ppl(item.user_id)"><v-icon color=white>remove</v-icon></v-btn>
              </v-list-tile-action>

            </v-list-tile>
            <v-divider inset></v-divider>
          </div>
        </v-list>

  </div>

</template>
<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
    props:{
      goinvite:false,
      team_id:''
    },
    computed:{
      getinvited(){
        return this.Invited
      },
      team(){
        return this.$store.getters.getteam;
      },
      profile(){
        return this.$store.getters.getProfile;
      }
    },
    data: () => ({
      isLoading: false,
      items: [],
      invited:[],
      model:null,
      search:null,
    }),
    methods:{
      remove_ppl:function(id){
         this.invited=this.invited.filter(function(item) {
          return item.user_id != id;
      });
      }
    },
    watch: {
      goinvite(invite){
        if(invite && this.invited.length>0){
          for (var i = 0; i < this.invited.length; i++) {
            this.$http.post(this.host+'/api/Invites/',{
              invite_id:'',
              user_id:this.invited[i].user_id,
              team_id:this.team_id
            }).then(response => {
              this.snackbar=true;
            }, response => {

            });
          }
        }
      },
      model(val){
        if(val!==null || val===''){
          var added = this.items.find(item => item.user_id === val);
          var will_add=true;
          if(this.invited.length>0){
            for (var i = 0; i < this.invited.length; i++) {
              if (this.invited[i].user_id=== added.user_id){
                will_add=false;
              }
            }
          };
          if(will_add){
            if(added.user_id!=this.profile.user_id){
              this.invited.push(added);
            }
          }
        }

      },
      search (val) {
        try {
          if(val!==null){
            this.isLoading=true;
            this.$http.get(this.host+'/api/User/Invite/'+ val).then(data => {
              this.items=data.body;
              this.isLoading=false
            }, data => {

            });
          }
        }
        catch(err) {
          console.log(err);
        }
      }
    }
  }
</script>
