<template>
  <v-container fluid grid-list-sm class="pa-0">
    <v-layout row wrap>
      <!-- <v-flex hidden-xs-sm-only md1 lg1>

      </v-flex> -->
      <v-flex xs12 sm12 md12 lg12 >
        <v-btn
          fab
          fixed
          small
          color="accent"
          style="bottom:40px;right:38px;"
          class=" primary--text"
          @click="getScriptTask();"
        >
          <v-icon color="white">autorenew</v-icon>
        </v-btn>
        <div>
          <input type="file" style="display: none;" ref="filepicker" @change="uploadFile" />
          <v-data-table
            :headers="headers"
            :items="this.myscripts"
            :search="search"
            class="elevation-1"
          >
            <template slot="items" slot-scope="props">
              <td class="text-xs-left">{{ props.item.anime_name }}</td>
              <td class="text-xs-left">{{ props.item.episode }}</td>
              <td class="text-xs-left">{{ props.item.updated_date }}</td>
              <td class="text-xs-right">
                <!-- <v-progress-linear v-model="valueDeterminate"></v-progress-linear> -->
                <v-btn-toggle multiple :value='[0]'>
                  <v-btn flat @click='filechoose(props.item.task_id)'>
                    <span>Upload</span>
                    <v-icon color="green">cloud_upload</v-icon>
                  </v-btn>
                </v-btn-toggle>
              </td>
            </template>
          </v-data-table>
          </div>
      </v-flex>
      <!-- <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex> -->
    </v-layout>
    <v-dialog v-model="snackbar" persistent max-width="290">
      <v-card dark>
        <v-card-title>{{uploadprogress}}% complete.</br>Please wait until uploading is complete!</v-card-title>
      </v-card>
    </v-dialog>

  </v-container>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  data(){
    return{
      clicked_task:'',
      myscripts:[],
      uploadprogress:0,
      snackbar: false,
      timeout: 6000,
      upload:{},
      totalpages:7,
      search:'',
      pagination: {},
        headers:[
            {
              text: 'Anime Name',
              align: 'left',
              value: 'name'
            },
            { text: 'Episode', value: 'episode', align:'left' , sortable:false },
            {text: 'Requested Date', value:'date', align: 'left'},
            { text: 'Actions', value: 'action', align:'right', sortable:false}
        ]
    }
  },
  computed:{
    profile(){
      return this.$store.getters.getProfile;
    },
    pages () {
        if (this.pagination.rowsPerPage == null ||
          this.pagination.totalItems == null
        ) return 0

        return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
      }
  },
  methods:{
    filechoose:function(task){
      this.clicked_task=task;
      this.$refs.filepicker.click();
    },
    uploadFile(e) {
			const input = this.$refs.filepicker;
			const file = input.files[0];
      this.$refs.filepicker.value='';
      if(file.size<=40960000){
        var formData = new FormData();
        formData.append('script_file', file, file.name);
        this.snackbar=true;
        var _this=this;
        var now = new Date();
        now=now.getDay() + "/" + now.getMonth() + "/" + now.getFullYear();
        this.$http.post(this.host+'/api/file?id='+ this.clicked_task + '&date=' + now, formData, {
            progress(e) {
                if (e.lengthComputable) {
                    _this.uploadprogress=Math.round(e.loaded / e.total * 100);
                }
            }
        }).then(()=>{
          this.snackbar=false;
          this.getScriptTask();
        });

      }else{
        alert( file.name + "File size must be lower than 40 Mb" + (file.size / 1024000));
      }
    },
    getScriptTask(){
      this.$http.get(this.host+'/api/Task?id='+ this.profile.user_id+ '&type=Scripting').then(data => {
        this.myscripts=data.body;
      });
    }
  },
  created(){
    this.getScriptTask();
  }
}
</script>
