<template>

</template>

<script>
import task from './task.vue';
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  props:{
    project_id:'',
    addtoproject:false
  },
  components:{
    'task':task
  },
  computed:{
    team(){
      return this.$store.getters.getteam;
    }
  },
  data () {
      return {
        model:'',
        task_id:[],
        search:'',
        isLoading:false,
        anime:{},

      }
  },
  methods:{
    getLowQualityimg:function(url){
      return [url.slice(0, 28), 'r/80x120/', url.slice(28)].join('');
    },

  },
  created(){
    // this.anime={id:'1',title:'akame', type: 'TV', episodes: 12, rated:'PG-13', synopsis: 'sdfdsffsfsdlll', image_url:'https://randomuser.me/api/portraits/men/35.jpg'}
  },
  watch: {
    search (val) {
      try {
          this.$http.get('https://api.jikan.moe/v3/search/anime?q=' + encodeURI(val) + '&limit=5&genre=12&genre_exclude=0').then(function(data){
          this.items=data.body.results;
          this.isLoading=false
          });
      }
      catch(err) {
        this.items=[];
      }
    },
    addtoproject(val){
      if(val){
        this.postAnime();
      }
    }
  }
}
</script>

