<template lang="html">
  <div>
    <div class="text-xs-center pa-3" color="red">
      <v-btn slot="activator" icon large>
          <v-avatar size="70px" title >
          <img v-bind:src="profile.user_avatar">
          </v-avatar>
        </v-btn>
        <div class="text-xs-center pt-5">
            <v-list-tile-title class="text-xs-center">{{profile.user_name}}</v-list-tile-title>
            <v-list-tile-sub-title class="green--text" v-if="isEmpty(steam.team_id)">Normal User</v-list-tile-sub-title>
            <v-list-tile-sub-title class="green--text" v-else>Translator</v-list-tile-sub-title>
        </div>
    </div>
    <!-- Logout button-->
     <v-btn block color="secondary" class="ma-1" dark @click="logout_app();pushroute('/');">Log out</v-btn>
    <v-divider></v-divider>
    <v-list>

    <v-list-group prepend-icon="list" >

        <v-list-tile slot="activator">
          <v-list-tile-title>MY LISTS</v-list-tile-title>
        </v-list-tile>

        <v-list>
          <v-dialog
              v-model="create_dialog"
              width="500"
            >
            <v-list-tile slot="activator" >

              <v-list-tile-avatar>
                <v-icon class="pl-4">add</v-icon>
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title>Create new</v-list-tile-title>
              </v-list-tile-content>

            </v-list-tile>

              <v-card>
                <v-card-title
                class="secondary white--text headline"
                  primary-title
                  dark
                >
                  Create a new list
                </v-card-title>

                <v-text-field
                  v-model="list_name"
                  label="list name"
                  class="pa-2"
                  color="info"
                  single-line
                  outline
                  :rules="[rules.required, rules.name]"
                ></v-text-field>

                <v-divider></v-divider>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    color="accent"
                    flat
                    @click="dialog = false;createlist();"
                  >
                    Create
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
        </v-list>
        <v-list v-for="(n,index) in list" :key="index">

          <v-list-tile @click="pushroute('/list/'+n.list_id)" >

            <v-list-tile-avatar  >
              <v-icon class="pl-4">playlist_play</v-icon>
            </v-list-tile-avatar>

            <v-list-tile-content >
              <v-list-tile-title> {{n.list_name1}}</v-list-tile-title>
            </v-list-tile-content>

          </v-list-tile>
        </v-list>

    </v-list-group>
</v-list>
  <v-divider></v-divider>

    <v-list-tile @click="">
        <v-list-tile-action>
          <v-icon>group</v-icon>
        </v-list-tile-action>
          <v-list-tile-title>MY TEAM</v-list-tile-title>
    </v-list-tile>
    <!-- team button -->
    <v-list v-if="isEmpty(steam.team_id)">
      <v-list-tile  @click="createnewteam();" >

        <v-list-tile-avatar>
          <v-icon class="pl-4">add</v-icon>
        </v-list-tile-avatar>

        <v-list-tile-content>
          <v-list-tile-title>Form a new Team</v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>
      <v-dialog persistent width="80%" v-model='team_dialog' >

      <v-stepper v-model="e1" light >
       <v-stepper-header>
         <v-stepper-step :complete="e1 > 1" step="1">Step up Team Information</v-stepper-step>

         <v-divider></v-divider>

         <v-stepper-step :complete="e1 > 2" step="2">Invite Members</v-stepper-step>

         <v-divider></v-divider>

         <v-stepper-step step="3">Targeted Audience</v-stepper-step>
       </v-stepper-header>

       <v-stepper-items>
         <v-stepper-content step="1">
           <v-layout align-space-around justify-center row wrap fill-height>
             <v-flex xs12 sm12 md2 lg2 class="pa-2 pr-0">
               <v-img
                 :src='getuploadedImg'
                 aspect-ratio="1"
                 class="grey elevation-3 "
               >
                 <v-layout
                   fill-height
                   align-end justify-end
                 >
                 <v-btn fab small icon color="secondary" @click='filechoose'>
                   <v-icon>edit</v-icon>
                 </v-btn>
                 <input type="file" accept="image/*" style="display: none;" ref="imguploader" id="appfile_id" @change="uploadFile" />
                 </v-layout>
               </v-img>
             </v-flex>
             <v-flex xs12 sm12 md10 lg10 class="pa-1 pl-0" >
               <v-layout
                 align-space-around justify-end column fill-height
               >
               <div class="pt-1" style="height:100%;">
               <v-text-field
                 v-model='team.team_name'
                 label="Enter Team Name here"
                 dark
                 solo
                 background-color="secondary"
                 hide-details
               ></v-text-field>
                  <v-textarea
                   v-model='team.team_description'
                   solo
                   auto-grow
                   placeholder="Enter Team Description here....."
                 ></v-textarea>
               </div>

               </v-layout>
             </v-flex>
           </v-layout>
           <v-divider></v-divider>
          <v-layout align-end justify-end pt-2>
            <v-btn
              color="primary"
              v-show='showhidestepbtn'
              @click="e1=2;"
            >
              Next
            </v-btn>
            <v-btn flat @click="team_dialog=false;cancelcreate()">Cancel</v-btn>
          </v-layout>

         </v-stepper-content>

         <v-stepper-content step="2">

           <v-layout align-space-around justify-center row wrap fill-height>
             <v-flex xs12 sm12 md12 lg12 class="pa-0">
                 <invite-member :team_id='team.team_id' :goinvite='goinvite'></invite-member>
             </v-flex>
             <v-flex xs12 sm12 md12 lg12 class="pa-1 pl-0" >

             </v-flex>
           </v-layout>
           <v-divider></v-divider>
           <v-layout align-end justify-end pt-2>
             <v-btn
               color="primary"
               @click="e1=3"
             >
               Next
             </v-btn>
             <v-btn flat @click="team_dialog=false;cancelcreate()">Cancel</v-btn>
           </v-layout>
         </v-stepper-content>

         <v-stepper-content step="3">
           <v-layout row wrap align-center justify-end fill-height >
             <v-flex xs12>
               <v-alert
                 :value="true"
                 color="info"
                 icon="info"
                 outline
                 dismissible
               >
                 Please carefully select the trageted people and the language your team will be translating. This will help the search engine of the website and will help the experience of the users.
               </v-alert>
             </v-flex>
             <v-flex hidden-xs-sm-only md4 lg4>

             </v-flex>
              <v-flex xs12 sm12 md4 lg4 pt-2>
                <v-combobox
                  v-model="team.tran_country"
                  :items="country"
                  label="This team translate for people in"
                ></v-combobox>
              </v-flex>
              <v-flex hidden-xs-sm-only md4 lg4>

              </v-flex>
              <v-flex hidden-xs-sm-only md2 lg2>

              </v-flex>

               <v-flex xs12 sm12 md4 lg4>
                 <v-combobox
                   v-model="team.tran_lang"
                   :items="language"
                   label="All translation projects from this team will be in"
                 ></v-combobox>
               </v-flex>
               <v-flex hidden-xs-sm-only md4 lg4>

               </v-flex>
             </v-layout>
             <v-divider></v-divider>
           <v-layout align-end justify-end pt-2>
             <v-btn
               color="primary"
                @click="postTeam();"
             >
               Create
             </v-btn>
             <v-btn flat @click="team_dialog=false;cancelcreate()">Cancel</v-btn>
           </v-layout>
         </v-stepper-content>
       </v-stepper-items>
     </v-stepper>
    </v-dialog>
      <v-divider></v-divider>
    </v-list>
    <v-list v-else>
      <v-list-tile avatar @click="pushroute('/team/'+ steam.team_id )">
        <v-list-tile-title>{{steam.team_name}}</v-list-tile-title>
        <v-list-tile-avatar>
          <img v-bind:src='steam.team_img'>
        </v-list-tile-avatar>
      </v-list-tile>
      <v-list-tile avatar @click="pushroute('/mytasks')">
        <v-list-tile-title>My Tasks</v-list-tile-title>
        <v-list-tile-avatar>
          <v-icon>work</v-icon>
        </v-list-tile-avatar>
      </v-list-tile>
      <v-divider></v-divider>
    </v-list>


  </div>

</template>

