  methods:{
    isEmpty:function(value){
      return (value == null || value.length === 0);
    },
  logout_app:function(){
    var _store=this
    FB.getLoginStatus(function(response) {
       if (response.status === 'connected') {
         FB.logout(function(response) {
           _store.$store.dispatch('setProfile',{user_id:null, user_name:null, user_avatar:null, team_role:null});
           _store.$store.dispatch('setTeam',{team_id:null,team_name:null,team_img:null,team_description:null});
           _store.$store.dispatch('setList',[]);
           _store.team_dialog=false;
         });
       }
    });
    this.$store.dispatch('toggle_Login');
    bus.$emit('toggleappdrawer');
  },
