    }, response => {

    });
    // this.e1=0;
  },
  modifyuserinfo:function(){
    this.$http.post(this.host+'/api/User/',{user_id:this.profile.user_id,user_name: this.profile.user_name,user_avatar: this.profile.user_avatar, team_id:this.team.team_id,team_role:'admin'}).then(response => {
      this.create_dialog=false;
      this.getuserinfo({user_id:this.profile.user_id,user_name: this.profile.user_name,user_avatar: this.profile.user_avatar, team_id:this.team.team_id,team_role:'admin'});
    }, response => {

    });
  },
  uploadFile() {
    // var fileInput = document.getElementById('appfile_id');
    // var file= fileInput.files[0];
    const input = this.$refs.imguploader;
    const file = input.files[0];
    var formData = new FormData();
    formData.append('pic', file);
      this.$http.post(this.host+'/api/img/'+ this.team.team_id, formData).then(response => {
        this.team.team_img=this.host+'/api/Team/Img/Get/'+ this.team.team_id ;
      });
      // this.team.team_img='http://localhost:1748/api/Team/Img/Get/'+ this.team.team_id ;
  },
  getFileSize(size) {
    if (size < 1000000) return `${Math.ceil(size / 1024)} kb`;
    else if (size >= 1000000) return `${Math.ceil(size / 1024000)} mb`;
  },
}
}
</script>

