<template>
  <v-container fluid fluid grid-list-sm class="pa-0" >
    <v-layout row wrap>
      <v-btn
        fab
        fixed
        small
        color="accent"
        style="bottom:40px;right:38px;"
        class=" primary--text"
        @click="getIssues();"
      >
        <v-icon color="white">autorenew</v-icon>
      </v-btn>
      <!-- <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex> -->
      <v-flex xs12 sm12 md12 lg12 >
              <v-data-table
                :headers="headers"
                :items="myissues"
                class="elevation-1"
                item-key="task_id"
              >
                <template slot="items" slot-scope="props">
                  <tr @click="props.expanded = !props.expanded">
                  <td class="text-xs-left">{{ props.item.comment_subject }}</td>
                  <td class="text-xs-left">{{ props.item.anime_name }}</td>
                  <td class="text-xs-left">{{ props.item.episode }}</td>
                  <td class="text-xs-left">{{ props.item.updated_date }}</td>
                  <td class="text-xs-right">
                    <v-btn-toggle multiple :value='[0,1,2]'>
                      <v-btn @click='Download_script(props.item.task_id)'>
                        <span>Download</span>
                        <v-icon color="blue">cloud_download</v-icon>
                      </v-btn>
                      <v-btn @click='filechoose(props.item.task_id)'>
                        <span>Fixed</span>
                        <v-icon color="green">check</v-icon>
                      </v-btn>
                    </v-btn-toggle>
                  </td>
                </tr>
                </template>
                <template slot="expand" slot-scope="props">

                  <v-card light class="ma-2">

                    <v-card-title class="yellow accent-3">
                        {{props.item.comment}}
                    </v-card-title>
                  </v-card>

                </template>
              </v-data-table>
              <input type="file" style="display: none;" ref="filepickerissue" @change="uploadFile" />
      </v-flex>
      <!-- <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex> -->
    </v-layout>
    <v-dialog v-model="snackbar" persistent max-width="290">
      <v-card dark>
        <v-card-title>{{uploadprogress}}% complete.</br>Please wait until uploading is complete!</v-card-title>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  computed:{
    profile(){
      return this.$store.getters.getProfile;
    },
  },
  data(){
    return{
      snackbar:false,
      uploadprogress:0,
      myissues:[],
        headers:[
            {text: 'Issue', value:'issue', align: 'left'},
            {
              text: 'Anime Name',
              align: 'left',
              value: 'name'
            },
            { text: 'Episode', value: 'episode', align:'left', sortable:false },
            {text: 'Updated Date',value:'date', align: 'left'},
            {text: 'Actions', value: 'action', align: 'right', sortable:false}
        ]
    }
  },
  methods:{
    getIssues(){
      this.$http.get(this.host+'/api/Task?id='+ this.profile.user_id+ '&type=Issue').then(data => {
        this.myissues=data.body;
      });
    },
    approve_now(task_id){
      var _this=this;
      var now = new Date();
      now=now.getDay() + "/" + now.getMonth() + "/" + now.getFullYear();
      this.$http.post(this.host+'/api/Task/Approve?id='+ task_id + '&type=Complete&date='+ now).then(data => {
        _this.getIssues();
      });
    },
    Download_script(task_id){
      window.open(this.host+'/api/file/'+task_id);
    },
    filechoose:function(task){
      this.clicked_task=task;
      this.$refs.filepickerissue.click();
    },
    uploadFile(e) {
			const input = this.$refs.filepickerissue;
			const file = input.files[0];
      this.$refs.filepickerissue.value='';
      if(file.size<=40960000){
        var formData = new FormData();
        formData.append('script_file', file, file.name);
        this.snackbar=true;
        var _this=this;
        var now = new Date();
        now=now.getDay() + "/" + now.getMonth() + "/" + now.getFullYear();
        this.$http.post(this.host+'/api/file?id='+ this.clicked_task + '&date=' + now, formData, {
            progress(e) {
                if (e.lengthComputable) {
                    _this.uploadprogress=Math.round(e.loaded / e.total * 100);
                }
            }
        }).then(()=>{
          this.snackbar=false;
          this.getIssues();
        });

      }else{
        alert( file.name + "File size must be lower than 40 Mb" + (file.size / 1024000));
      }
    },
  },
  created(){
    this.getIssues();
  }
}
</script>
