<template>
  <v-content>
    <div v-if="checkadmin()">
      <v-btn fab fixed bottom right class="mr-2 mt-1 accent" @click="addproject=true;createnewproject();">
        <v-icon>add</v-icon>
      </v-btn>
    </div>
    <v-card >
      <v-layout pl-2 pr-2>
        <v-flex hidden-xs-sm-only md4 lg7>

        </v-flex>

        <v-flex xs5 sm3 md3 lg1 >
          <v-select
          :items="['All', 'Completed', 'Ongoing']"
          hide-details
          v-model="selected"
          class="pr-2"
          style="paddingTop:12px;"
          >
          </v-select>
        </v-flex>

        <v-flex xs7 sm9 md5 lg4 style="justify-content:right;">
            <v-text-field
              v-model="searchtable"
              append-icon="search"
              label="Search"
              single-line
              hide-details
              width=100
              color="accent"
            ></v-text-field>

        </v-flex>
      </v-layout>


        <v-dialog width=80% persistent v-model="addproject">
          <v-card>
          <v-layout row wrap>
            <v-flex xs12 sm12 md12 xl12>
              <v-autocomplete
                pa-1
                v-model="model"
                :items="items"
                :loading="isLoading"
                :search-input.sync="search"
                hide-details
                hide-selected
                item-text="title"
                item-value="mal_id"
                label="Search Anime....."
                solo
                dark
                @change='setAnime'
              >
                <template slot="no-data">
                  <v-list-tile>
                    <v-list-tile-title>
                      Select
                      <strong>anime</strong>
                    </v-list-tile-title>
                  </v-list-tile>
                </template>

                <template
                  slot="item"
                  slot-scope="{ item, tile }"
                >
                  <v-list-tile-avatar
                    color="indigo"
                    class="headline font-weight-light white--text"
                  >

                    <img :src='item.image_url' alt="avatar" width="100">
                  </v-list-tile-avatar>

                  <v-list-tile-content>
                    <v-list-tile-title v-text='item.title'></v-list-tile-title>
                    <v-list-tile-sub-title v-text="item.type"></v-list-tile-sub-title>
                  </v-list-tile-content>
                  <v-list-tile-action>
                    <v-icon>mdi-coin</v-icon>
                  </v-list-tile-action>
                </template>
              </v-autocomplete>
            </v-flex>
            <v-flex xs12 sm12 md12 lg12 xl12 >
              <v-layout  row wrap fill-height >
                <v-flex xs12 sm12 md2 lg2 xl2 class="pt-2 pl-2">
                  <v-card class="elevation-0 " dark>
                    <v-img
                      :src="this.anime.image_url"
                      aspect-ratio="0.8"
                      class="grey lighten-2"
                      @click="dialog=true"
                    >
                      <v-layout
                        slot="placeholder"
                        fill-height
                        align-center
                        justify-center
                        ma-0
                      >
                        <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                      </v-layout>
                    </v-img>
                  </v-card>
                </v-flex>
                <v-flex xs12 sm12 md10 lg10 xl10>
                  <v-card class="ma-0 elevation-0 pa-2 mt-2">
                  <v-card-text class="pa-2"><span class="subheading accent--text">Name : </span><span> {{anime.title}}</span></v-card-text>
                  <v-card-text class="pa-2"><span class="subheading accent--text">Type : </span><span> {{anime.type}}</span></v-card-text>
                  <v-card-text class="pa-2"><span class="subheading accent--text">Episodes : </span><span> {{anime.episodes}}</span></v-card-text>
                  <v-card-text class="pa-2"><span class="subheading accent--text">Rating : </span><span> {{anime.rated}}</span></v-card-text>
                  <v-card-text class="pa-2"><span class="subheading accent--text">Description : </span><span>{{anime.synopsis}}</span></v-card-text>
                  </v-card>
                </v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
        </v-card>
        <v-card>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat @click='addproject=false;cancelcreate();'>Cancel</v-btn>
            <v-btn color="secondary" @click='postAnime();'>Add</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <v-data-table
        :headers="project_headers"
        :items="filter_table"
        :search="searchtable"
      >
        <template slot="items" slot-scope="props">
            <tr @click="pushroute('/projects/' + props.item.project_id+ '/' + props.item.anime_id)">
            <td class="pl-2 pt-2" width="200"><img :src='props.item.anime_img' width="160px"></td>
            <td class="text-xs-left"  >{{ props.item.anime_name }}</td>
            <td class="text-xs-left" >{{ props.item.anime_type }}</td>
            <td class="text-xs-left" >{{ props.item.anime_episodes }}</td>
            <td class="text-xs-left" >{{ props.item.finished_episodes }}</td>
            <td class="text-xs-right">
                <v-progress-circular
                  :rotate="-90"
                  :size="100"
                  :width="15"
                  :value='(props.item.finished_episodes/props.item.anime_episodes)*100'
                  color="accent"
                >
                  {{Math.round((props.item.finished_episodes/props.item.anime_episodes)*100)}} %
                </v-progress-circular>
            </td>
          </tr>
        </template>

        <v-alert slot="no-results" :value="true" color="error" icon="warning">
          Your search for "{{ searchtable }}" found no results.
        </v-alert>
      </v-data-table>
    </v-card>
  </v-content>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
import create from './create_dialog.vue'
export default {
  mixins:[commonmethods],
  components:{
    'create-project': create
  },
  data(){
    return{
      addproject:false,
      items:[],
      anime:{},
      project_id:'',
      selected:'All',
      searchtable:'',
      projects:[],
      isLoading:false,
      search:'',
      model:[],
      headers:[
          {
            text: 'Profile',
            align: 'left',
            sortable: false,
            value: 'profile'
          },
          { text: 'Name', value: 'name', align:'left' },
          { text: 'Role', value: 'role', align:'left' },
        ],
        project_headers:[
            {
              text: 'Image',
              align: 'left',
              sortable: false,
              value: 'img'
            },
            { text: 'Name', value: 'name', align:'left' },
            { text: 'Type', value: 'type', align:'left' },
            { text: 'Episodes', value: 'epi', align:'left' },
            { text: 'Episodes finished', value: 'epi', align:'left' },
            { text: 'Over All Progress', value: 'prog', align: 'right'}
        ]
    }
  },
  computed:{
    myteam(){
      return this.$store.getters.getteam;
    },
    filter_table(){
      if(this.selected=='All'){
        return this.projects;
      }else if(this.selected=='Completed'){
        return this.projects.filter((i)=>{
          return i.anime_episodes==i.finished_episodes;
        });
      }else{
        return this.projects.filter((i)=>{
          return i.anime_episodes>i.finished_episodes;
        });
      }
    },
  },
  methods:{
    checkadmin(){
      if(this.myteam.team_id!=this.$route.params.team_id){
        return false;
      }else{
        return true;
      }
    },
    createnewproject(){
      this.$http.post(this.host + '/api/Projects/', {team_id:this.myteam.team_id, finished_episodes: 0}).then(response => {
        this.project_id=response.body.project_id;
      }, response => {

      });
    },
    cancelcreate:function(){
      this.$http.delete(this.host + '/api/Projects/DeleteNull').then(function(data){
      });
    },
    setAnime:function(){
      this.anime=this.items.find(item => item.mal_id === this.model);
    },
    postAnime:function(){
      this.$http.post(this.host+ '/api/Anime',{anime_id:this.anime.mal_id, anime_name:this.anime.title, anime_type:this.anime.type, anime_episodes:this.anime.episodes,anime_synosis:this.anime.synopsis, anime_rating:this.anime.rated, anime_img:this.anime.image_url}).then(response => {
        this.anime= response.body;
      });
      this.$http.put(this.host+ '/api/Project/checkdup/', {anime_id: this.anime.mal_id, project_id: this.project_id, team_id: this.myteam.team_id, finished_episodes:0}).then(response => {
        this.getProjects()
      });
      this.addproject=false;
    },
    getProjects(){
      this.$http.get(this.host+'/api/Project/TeamProject/' + this.$route.params.team_id).then(function(data){
        this.projects=data.body;
      });
    }
  },
  watch: {
    search (val) {
      try {
          this.$http.get('https://api.jikan.moe/v3/search/anime?q=' + encodeURI(val) + '&limit=5&genre=12&genre_exclude=0').then(function(data){
          this.items=data.body.results;
          this.isLoading=false
          });
      }
      catch(err) {
        this.items=[];
      }
    },
  },
  created(){
    this.getProjects();
  }
}
</script>

<style lang="css">
</style>
