<template>
  <v-content>
    <v-btn
      fixed
      dark
      fab
      right
      style="bottom:15px;right:13px;"
      color="accent"
      @click="pushroute('/search')"
      >
        <v-icon>arrow_back</v-icon>
     </v-btn>
     <v-btn
       fixed
       dark
       fab
       right
       small
       style="bottom:83px;right:21px;"
       color="accent"
       @click="fb_share('/subbed/'+ $route.params.anime_id +'/' + $route.params.project_id)"
       >
         <v-icon>share</v-icon>
      </v-btn>
  <v-layout row wrap>
    <v-flex xs12 sm3 md2 lg2 class="mb-2">
      <v-card class="elevation-5 mt-1 ml-1 mr-1" style="height:100%" >
        <v-autocomplete
          v-model='selected'
          :items="translators"
          hide-details
          item-text="team_name"
          item-value="project_id"
          box
          label="Translator"
          class="elevation-5 "
          color="accent"
          @change='dropdown_change'
        >

          <template
            slot="item"
            slot-scope="{ item, tile }"
          >
            <v-list-tile-content>
              <v-list-tile-title v-text='item.team_name'></v-list-tile-title>
            </v-list-tile-content>
          </template>
        </v-autocomplete>
      <v-img style="width:100%;" :src="anime.anime_img" ></v-img>
      <table width="100%" class="pa-2">
        <tr>
          <td width="40%">
            <span class="subheading font-weight-bold secondary--text">Type</span>
          </td>
          <td>
            <span class="subheading black--text"> : {{anime.anime_type}}</span>
          </td>
        </tr>
        <tr>
          <td>
            <span class="subheading font-weight-bold secondary--text">Episodes</span>
          </td>
          <td>
            <span class="subheading black--text"> : {{anime.anime_episodes}}</span>
          </td>
        </tr>
        <tr>
          <td>
            <span class="subheading font-weight-bold secondary--text">Rating</span>
          </td>
          <td>
            <span class="subheading black--text"> : {{anime.anime_rating}}</span>
          </td>
          <tr>
            <td colspan="2" style="paddingTop:10px">

              <v-divider class="pa-1"></v-divider>
              <span class="subheading font-weight-bold secondary--text">Synosis</span>
            </td>
          </tr>

          <tr>
            <td colspan="2" style="text-align: justify">{{anime.anime_synosis}}</td>
          </tr>
        </tr>
      </table>
      <v-spacer></v-spacer>
    </v-card>
    </v-flex>
    <v-flex xs12 sm9 md10 lg10>
    <v-layout column wrap style="height:93vh;overflow-y:scroll;">
      <v-card class="elevation-5 ma-1" style="height:100%">
        <v-list>
          <template
           v-for="item in tran_episodes">
            <v-list-tile
              avatar
              @click="download(item.video_link)"
              :key="item.episode"
            >
              <v-list-tile-content>
                <v-list-tile-title>Episode - {{ item.episode }} {{getlable(item.task_name)}}</v-list-tile-title>
                <v-list-tile-sub-title>Translated by - {{ item.user_name }}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider class="mb-0"></v-divider>
          </template>
      </v-list>
      </v-card>
    </v-layout>
    </v-flex>
  </v-layout>
</v-content>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  data(){
    return{
      tran_episodes: [],
      translators:[],
      selected:'',
      anime:[],
    }
  },
  methods:{
    dropdown_change(){
      this.$http.get(this.host+'/api/Anime/GetSubs?anime_id='+this.$route.params.anime_id+"&project_id="+ this.selected).then((data)=>{
        this.tran_episodes=data.body;
      })
    },
    getlable(task){
      if(task==='Completed'){
        return "";
      }else{
        return " ( " + task + " in progress. )"
      }
    },
    download(link){
      if(link!=null){
        window.open(link);
      }
    },
    fb_share:function(content){
      console.log(content);
      FB.ui({
        method: 'share',
        mobile_iframe: true,
        href: 'https://localhost:8080' + content,
      }, function(response){});
    },
  },
  created(){
    this.$http.get(this.host+'/api/Anime/'+this.$route.params.anime_id).then((data)=>{
      this.anime=data.body;
    });
    this.$http.get(this.host+'/api/Anime/GetTranslators/'+this.$route.params.anime_id).then((data)=>{
    this.translators=data.body;
    if(this.$route.params.project_id=='null'){
      this.selected=data.body[0].project_id;
    }else{
      this.selected=parseInt(this.$route.params.project_id);
    }
    this.dropdown_change();
    })
  }
}
</script>

<style lang="css">
</style>
