<template>
  <v-layout row dark>
    <v-flex >
      <v-card dark max-height=500 width=300 >
        <v-list two-line>
          <template v-for="item in invites">
            <v-subheader>
              Invitations
            </v-subheader>
            <v-list-tile
              :key="item.invite_id"
              avatar
            >
              <v-list-tile-avatar>
                    <img :src= 'host +"/api/Team/Img/Get/"+ item.team_id'>
              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title v-html="item.team_name"></v-list-tile-title>
                <v-list-tile-sub-title>Come join to out team!</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="success" flat @click="join(item.team_id,item.invite_id)"> Join </v-btn>
              <v-btn color="error" flat @click="decline(item.invite_id)"> Decline </v-btn>
            </v-card-actions>
          </template>
          <v-divider></v-divider>
          <template v-for="item in notification">
            <v-subheader>
              Notification
            </v-subheader>
            <v-list-tile
              :key="item.noti_id"
              avatar
              @click=""
            >
              <v-list-tile-avatar>
                <div v-if="item.noti_type==='issue'">
                  <v-btn icon flat>
                    <v-icon color="indigo">bug_report</v-icon>
                  </v-btn>
                </div>
                <div v-if="item.noti_type==='script'">
                  <v-btn icon flat>
                    <v-icon color="indigo">code</v-icon>
                  </v-btn>
                </div>
                <div v-if="item.noti_type==='quality'">
                  <v-btn icon flat>
                    <v-icon color="indigo">gavel</v-icon>
                  </v-btn>
                </div>
                <div v-if="item.noti_type==='encode'">
                  <v-btn icon flat>
                    <v-icon color="indigo">movie_filter</v-icon>
                  </v-btn>
                </div>
                <div v-if="item.noti_type==='tune'">
                  <v-btn icon flat>
                    <v-icon color="indigo">exposure</v-icon>
                  </v-btn>
                </div>

              </v-list-tile-avatar>

              <v-list-tile-content>
                <v-list-tile-title v-html="item.noti_type"></v-list-tile-title>
                <v-list-tile-sub-title>{{item.project_name}} - episode {{item.episode}}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </template>
        </v-list>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
    props:{
      notification:{},
      invites:{}
    },
    created(){

    },
    computed:{
      profile(){
        return this.$store.getters.getProfile;
      }
    },
    methods:{
      join:function(teamid,id){
        this.$http.post(this.host+'/api/User',{
          user_id:Number(this.profile.user_id),
          user_name: this.profile.user_name,
          user_avatar: this.profile.user_avatar,
          team_id:teamid,
          team_role:'member'
        }).then(function(data){
          this.getuserinfo({user_id:this.profile.user_id,user_name: this.profile.user_name,user_avatar: this.profile.user_avatar, team_id:data.body.team_id,team_role:data.body.team_role});
          this.$http.delete(this.host+'/api/Invites/' + id).then(function(data){
          });
        });
      },
      decline:function(id){
        this.$http.delete(this.host+'/api/Invites/' + id).then(function(data){
        });
      }
    }
  }
</script>
