import searchArea from './components/SearchResults/SearchResultArea.vue';
import list from './components/SearchResults/List.vue';
import home from './components/AppHome.vue';
import getsubbed from './components/SearchResults/SubLink.vue';
import teamHome from './components/Team/TeamView.vue';
import teamprofile from './components/Team/TeamProfile.vue';
import tasks from './components/Tasks/MyTasks.vue';
import projectdetails from './components/Team/ProjectDetails.vue';
import projects from './components/Team/Projects.vue';
export default[
  {path:'/project/:project_id/:anime_id', component:projectdetails},
  {path:'/search', component:searchArea},
  {path:'/', component:home},
  {path:'/mytasks', component:tasks},
  {path:'/subbed/:anime_id/:project_id', component:getsubbed},
  {path:'/list/:listid', component:list},
  {path:'/projects/:team_id', component:projects},
  {path:'/team/:team_id', component:teamprofile}
]
