<template>
  <v-container fluid fluid grid-list-sm class="pa-0">
    <v-layout row wrap>
      <v-dialog v-model="dialog" max-width="400">
        <v-card>
          <v-text-field v-model='comment_subject' label="Error Name" full-width hide-details required color="accent"></v-text-field>
          <v-divider></v-divider>
          <v-textarea
            v-model='comment'
            full-width
            rows=15
            color="white"
            placeholder="Write down comments here...."
           ></v-textarea>
           <v-divider></v-divider>
          <v-card-actions>
            <v-btn color="accent" flat @click="report_issue()">Report</v-btn>
            <v-spacer></v-spacer>
            <v-select
              :items="['To Scripter', 'To Encoder']"
              color="accent"
              v-model="report_to"
            ></v-select>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-btn
        fab
        fixed
        small
        color="accent"
        class=" primary--text"
        style="bottom:40px;right:38px;"
        @click="getFineTune();"
      >
        <v-icon color="white">autorenew</v-icon>
      </v-btn>
      <!-- <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex> -->
      <v-flex xs12 sm12 md12 lg12 >
        <div>
          <v-data-table
            :headers="headers"
            :items="myfinetune"
            :search="search"
            class="elevation-1"
          >
            <template slot="items" slot-scope="props">
              <td class="text-xs-left">{{ props.item.anime_name }}</td>
              <td class="text-xs-left">{{ props.item.episode }}</td>
              <td class="text-xs-left">{{ props.item.updated_date }}</td>
              <td class="text-xs-right">
                <v-btn-toggle multiple :value='[0,1,2]'>
                  <v-btn flat @click='Download_Video(props.item.video_link);'>
                    <span>Download</span>
                    <v-icon color="blue">cloud_download</v-icon>
                  </v-btn>
                  <v-btn flat @click='open_dialog(props.item.task_id)'>
                    <span>Error Report</span>
                    <v-icon color="red darken-3">bug_report</v-icon>
                  </v-btn>
                  <v-btn flat @click='publish(props.item.task_id, props.item.project_id, props.item.episode)'>
                    <span>Publish</span>
                    <v-icon color="green">publish</v-icon>
                  </v-btn>
                </v-btn-toggle>
              </td>
            </template>
          </v-data-table>
          </div>
      </v-flex>
      <!-- <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex> -->
    </v-layout>
  </v-container>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  data(){
    return{
      report_to:'To Encoder',
      dialog:false,
      comment:'',
      comment_subject:'',
      fallback:'',
      myfinetune:[],
      totalpages:7,
      search:'',
      pagination: {},
        headers:[
            {
              text: 'Anime Name',
              align: 'left',
              value: 'name'
            },
            { text: 'Episode', value: 'episode', align:'left' , sortable:false },
            {text: 'Updated Date', value:'date', align: 'left'},
            { text: 'Actions', value: 'action', align:'right', sortable:false}
        ]
    }
  },
  computed:{
    profile(){
      return this.$store.getters.getProfile;
    },
    pages () {
        if (this.pagination.rowsPerPage == null ||
          this.pagination.totalItems == null
        ) return 0

        return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
      },
  },
  methods:{
    getFineTune(){
      this.$http.get(this.host+'/api/Task?id='+ this.profile.user_id+ '&type=Fine Tune').then(data => {
        this.myfinetune=data.body;
      });
    },
    approve_now(task_id){
      var _this=this;
      var now = new Date();
      now=now.getDay() + "/" + now.getMonth() + "/" + now.getFullYear();
      this.$http.post(this.host+'/api/Task/Approve?id='+ task_id + '&type=Completed&date='+ now).then(data => {
        _this.getFineTune();
      });
    },
    Download_Video(link){
      window.open(link);
    },
    open_dialog(task_id){
      this.dialog=true;
      this.task_id=task_id;
    },
    report_issue(){
      var _this=this;
      this.$http.post(this.host+'/api/Task/Bug?id='+ this.task_id + '&comment_subject=' + this.comment_subject + '&comment=' + this.comment + '&to=' + this.report_to).then(data => {
        _this.getFineTune();
        _this.dialog=false;
        _this.comment_subject="";
        _this.comment="";
      });
    },
    publish(task_id, project_id, epi){
      var _this=this;
      var now = new Date();
      now=now.getDay() + "/" + now.getMonth() + "/" + now.getFullYear();
      this.$http.put(this.host+'/api/Tasks',{
        task_id:task_id,
        task_name:"Completed",
        updated_date:now,
      }).then(()=>{
        _this.getFineTune();
      });
    },
  },
  created(){
    this.getFineTune();
  }
}
</script>
