<template>
  <v-content class="pt-4">
    <v-card  class="mt-3 pt-3" >
      <v-layout row wrap>
        <v-flex xs12 sm3 md2 lg2 pt-2 pl-1>
          <v-card style="height:100%">
          <v-card-title
            class=" font-weight-bold accent--text title pl-1 "
          >
          {{anime[0].anime_name}}
          </v-card-title>
          <v-img style="width:100%;" :src="anime[0].anime_img" ></v-img>
          <table width="100%" class="pt-2 pl-1">
            <tr>
              <td width="40%">
                <span class="subheading font-weight-bold secondary--text">Type</span>
              </td>
              <td>
                <span class="subheading black--text"> : {{anime[0].anime_type}}</span>
              </td>
            </tr>
            <tr>
              <td>
                <span class="subheading font-weight-bold secondary--text">Episodes</span>
              </td>
              <td>
                <span class="subheading black--text"> : {{anime[0].anime_episodes}}</span>
              </td>
            </tr>
            <tr>
              <td>
                <span class="subheading font-weight-bold secondary--text">Rating</span>
              </td>
              <td>
                <span class="subheading black--text"> : {{anime[0].anime_rating}}</span>
              </td>
            </tr>
          </table>
            <v-btn
              fab
              fixed
              bottom
              right
              small
              color="accent"
              @click="pushroute('/team/'+ team.team_id)"
            >
              <v-icon>arrow_back</v-icon>
            </v-btn>
        </v-card>
        </v-flex>
        <v-flex xs12 sm9 md10 lg10>
        <v-layout row wrap style="height:92vh;overflow-y:scroll;" class="elevation-7 ma-2">
          <v-flex pl-2 pt-1>
            <v-list>
              <task v-for="a in anime[0].anime_episodes" :key="a"
              :anime='find_task(a)'
              ></task>
            </v-list>
          </v-flex>
        </v-layout>
        </v-flex>
      </v-layout>

  </v-card>
        <!-- <app-detail :project_id='this.$route.params.projectid' :anime_id='this.$route.params.animeid'></app-detail> -->
  </v-content>
</template>

<script>
import task from './task.vue';
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  components:{
    'task':task
  },
  computed:{
    team(){
      return this.$store.getters.getteam;
    }
  },
  data () {
    return {
      anime:[{anime_name:'', anime_img:'',anime_type:'', anime_rating:''}]
    }
  },
  methods:{
    find_task:function(val){
      return this.anime.find(item => item.episode === val);
    }
  },
  created(){
    var _this=this;
    this.$http.put(this.host+'/api/Tasks/GetTask',{project_id: this.$route.params.projectid, anime_id: this.$route.params.animeid}).then(function(data){
      _this.anime=data.body;
    });
  },
}
</script>
