<template >
    <div v-if='anime.script===null'>
      <div v-show='showmodel' class="pa-1">
          <v-card>
            <v-card-title primary-title  class="white--text secondary subheading ">
            <v-list-tile-content>
              EPISODE - {{this.anime.episode}}
            </v-list-tile-content>
            </v-card-title>
          </v-card>
        <v-layout row wrap>
          <v-flex xs3 sm3 md3 lg3 xl3>
            <v-icon large class="pl-2 pt-2">code</v-icon>
          </v-flex>
          <v-flex xs3 sm3 md3 lg3 xl3>
            <v-icon large class="pl-2 pt-2">gavel</v-icon>
          </v-flex>
          <v-flex xs3 sm3 md3 lg3 xl3>
            <v-icon large class="pl-2 pt-2">movie_filter</v-icon>
          </v-flex>
          <v-flex xs3 sm3 md3 lg3 xl3 >
            <v-icon large class="pl-2 pt-2">exposure</v-icon>
          </v-flex>


          <v-flex xs3 sm3 md3 lg3 xl3 pa-2 v-for="i in 4" :key="i">
            <v-autocomplete
              v-model='task[i]'
              :items="members"
              hide-details
              item-text="user_name"
              item-value="user_id"
              :label='getlable(i)'
              box
              class="elevation-5 "
              color="accent"
              @change='updatestatus'
            >

              <template
                slot="item"
                slot-scope="{ item, tile }"
              >
                <v-list-tile-avatar>
                  <img :src='item.user_avatar' alt="avatar" width="100">
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title v-text='item.user_name'></v-list-tile-title>
                </v-list-tile-content>
              </template>
            </v-autocomplete>
          </v-flex>
        </v-layout>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn flat color="orange" :disabled='this.state' @click="addtoproject">Deliver Task</v-btn>
          <v-btn flat color="orange" @click="linkdialog=true" >Upload Completed Link</v-btn>
        <v-dialog
            v-model="linkdialog"
            width="500"
          >
            <v-card>
              <v-card-title
              class="secondary white--text headline"
                primary-title
              >
                Script Link
              </v-card-title>

              <v-text-field
                v-model='link'
                label="Paste Script Link Here"
                class="pa-2"
                color="info"
                single-line
                outline
              ></v-text-field>
              <v-divider></v-divider>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="accent"
                  flat
                  @click="postlink()"
                >
                  Post
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-card-actions>
        <v-divider></v-divider>
        </div>
        </div>
      </div>
    </div>
    </div>
    <div v-else class="pt-1 pr-1 pb-2">
      <div v-if='progress'>
        <v-list-tile
          @click=""
        >
        <v-list-tile-content>
          <v-list-tile-title>EPISODE - {{anime.episode}}</v-list-tile-title>
          <v-list-tile-sub-title>{{anime.task_name}}</v-list-tile-sub-title>
        </v-list-tile-content>
          <v-spacer></v-spacer>
          <v-progress-circular
          class="pa-1"
          background-color="blue-grey"
          color="green"
          :value='getvalue(anime.task_name)'
          >
          </v-progress-circular>
          </v-list-tile>
          <v-divider></v-divider>
      </div>
      <div v-else >
        <v-list-tile
         @click="download(anime.video_link)"
        >
        <v-list-tile-content>
          <v-list-tile-title>EPISODE - {{anime.episode}}</v-list-tile-title>
          <v-list-tile-sub-title>{{anime.task_name}}</v-list-tile-sub-title>
        </v-list-tile-content>

        <v-spacer></v-spacer>
        <v-icon color=green size=30>
          check
        </v-icon>
        </v-list-tile>
        <v-divider></v-divider>
      </div>
    </div>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  props:{
    anime:{},
  },
  computed:{
    members(){
     return this.$store.getters.getmembers;
   },
   profile(){
     return this.$store.getters.getProfile;
   },
   team(){
     return this.$store.getters.getteam;
   },
   isinprocess(){
    try {
      if(this.return_task.script==="script"){
        return false;
      }else{
        return true;
      }
    } catch (e) {
      return false;
    }
   },
  },
  data(){
    return{
      getinfo:false,
      showmodel:true,
      task:[],
      state:true,
      return_task:{},
      linkdialog:false,
      link:'',
      progress:true,
    }
  },
  methods:{
    postlink:function(){
      var now = new Date();
      now=now.getDay() + "/" + now.getMonth() + "/" + now.getFullYear();
      this.$http.put(this.host+'/api/Tasks',{
        task_id:this.anime.task_id,
        script:this.$store.getters.getProfile.user_id,
        task_name:"Completed",
        video_link:this.link,
        updated_date:now,
      }).then(function(){
        this.linkdialog=false;
        this.showmodel=false;
      });

    },

    getmembername:function(id){
        return this.members.find(item => item.user_id === id).user_name;
    },
    getvalue:function(index){
      if(index==='Scripting'){
        return 20;
      }else if (index==='Quality Control'){
        return 40;
      }else if (index==='Encoding'){
        return 60;
      }else if (index==='Fine Tune'){
        return 80;
      }else if (index==='Completed'){
        this.progress=false;
        return 100;
      }
    },
    getlable:function(index){
      if(index===1){
        return 'Scripting';
      }else if (index===2){
        return 'Quality Control';
      }else if (index===3){
        return 'Encoder';
      }else{
        return 'Fine Tune';
      }
    },
    updatestatus:function(){
      if(this.task.length >=5){
        this.state =false;
      }else{
        this.state= true;
      }
    },
    createTask:function(date){
      this.$http.put(this.host+'/api/Tasks',{
        task_id: this.anime.task_id,
        script:this.task[1],
        quality_control:this.task[2],
        encoding:this.task[3],
        fine_tune:this.task[4],
        task_name:"Scripting",
        comment_subject:null,
        comment:null,
        script_link:null,
        video_link:null,
        project_id:this.anime.project_id,
        updated_date:date,
        episode:this.anime.episode,
        anime_id:this.anime.anime_id
      }).then(function(data){
      });
    },
    addtoproject:function(){
      var now = new Date();
      this.createTask(now.getDay() + "/" + now.getMonth() + "/" + now.getFullYear());
      this.showmodel=false;
      // this.getSingleTask();
    },
    download(link){
      window.open(link);
    },
    checkadmin(){
      if(this.profile.team_role===null || this.profile.team_role==='' || this.profile.team_role.length>5){
        return this.showmodel=false;
      }else{
        return this.showmodel=true;
      }
    },
  },
  mounted () {
    this.checkadmin();
    setTimeout(() => {
      this.$http.post(this.host+ '/api/Task/getByepisode/',{project_id:this.anime.project_id, finished_episodes:this.episode}).then(response => {
        this.return_task=response.body;
      }, response => {
        console.log("not get single task");
      });
  }, 3000)
},
}

</script>

<style lang="css">
</style>
