<template>
  <v-container fluid fluid grid-list-sm class="pa-0">
    <v-layout row wrap>
      <v-btn
        fab
        fixed
        small
        color="accent"
        style="bottom:40px;right:38px;"
        class=" primary--text"
        @click="getQuality_Control_Task();"
      >
        <v-icon color="white">autorenew</v-icon>
      </v-btn>
      <!-- <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex> -->
      <v-flex xs12 sm12 md12 lg12 >
        <div>
          <div>
            <v-dialog v-model="dialog"  max-width="400">
              <v-card>
                <v-text-field v-model='comment_subject' label="Error Name" full-width hide-details required color="accent"></v-text-field>
                <v-divider></v-divider>
                <v-textarea
                  v-model='comment'
                  full-width
                  rows=15
                  color="white"
                  placeholder="Write down comments here...."
                 ></v-textarea>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="accent" flat @click="report_issue()">Report</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </div>
          <v-data-table
            :headers="headers"
            :items="this.myquality"
            :search="search"
            class="elevation-1"
          >
            <template slot="items" slot-scope="props">
              <td class="text-xs-left">{{ props.item.anime_name }}</td>
              <td class="text-xs-left">{{ props.item.episode }}</td>
              <td class="text-xs-left">{{ props.item.updated_date }}</td>
              <td class="text-xs-right">
                <v-btn-toggle multiple :value='[0,1,2]'>
                  <v-btn flat @click='Download_script(props.item.task_id);'>
                    <span>Download</span>
                    <v-icon color="blue">cloud_download</v-icon>
                  </v-btn>
                  <v-btn flat @click='open_dialog(props.item.task_id);'>
                    <span>Report</span>
                    <v-icon color="red darken-3">bug_report</v-icon>
                  </v-btn>
                  <v-btn flat @click='approve_now(props.item.task_id);'>
                    <span>Approve</span>
                    <v-icon color="green" >gavel</v-icon>
                  </v-btn>
                </v-btn-toggle>
              </td>
            </template>
          </v-data-table>
          </div>
      </v-flex>
      <!-- <v-flex hidden-xs-sm-only md2 lg2>

      </v-flex> -->
    </v-layout>
  </v-container>
</template>

<script>
import commonmethods from '../../mixins/commonMethods';
export default {
  mixins:[commonmethods],
  data(){
    return{
      task_id:'',
      comment:'',
      comment_subject:'',
      dialog:false,
      myquality:[],
      totalpages:7,
      search:'',
      pagination: {},
        headers:[
            {
              text: 'Anime Name',
              align: 'left',
              value: 'nname'
            },
            { text: 'Episode', value: 'episode', align:'left' , sortable:false },
            {text: 'Updated Date', align: 'left', value:'update'},
            { text: 'Actions', value: 'action', align:'right', sortable:false}
        ]
    }
  },
  computed:{
    profile(){
      return this.$store.getters.getProfile;
    },
    pages () {
        if (this.pagination.rowsPerPage == null ||
          this.pagination.totalItems == null
        ) return 0

        return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
      }
  },
  methods:{
    getQuality_Control_Task(){
      this.$http.get(this.host+'/api/Task?id='+ this.profile.user_id+ '&type=Quality Control').then(data => {
        this.myquality=data.body;
      });
    },
    approve_now(task_id){
      var _this=this;
      var now = new Date();
      now=now.getDay() + "/" + now.getMonth() + "/" + now.getFullYear();
      this.$http.post(this.host+'/api/Task/Approve?id='+ task_id + '&type=Encoding&date='+ now+ '&video_link=null').then(data => {
        _this.getQuality_Control_Task();
      });
    },
    Download_script(task_id){
      window.open(this.host+'/api/file/'+task_id);
    },
    open_dialog(task_id){
      this.dialog=true;
      this.task_id=task_id;
    },
    report_issue(){
      var _this=this;
      this.$http.post(this.host+'/api/Task/Bug?id='+ this.task_id + '&comment_subject=' + this.comment_subject + '&comment=' + this.comment + '&to=script').then(data => {
        _this.getQuality_Control_Task();
        _this.dialog=false;
      });
    }
  },
  created(){
    this.getQuality_Control_Task();
  }
}
</script>
