import Vue from 'vue';
import Vuex from 'vuex';
import PersistedState from 'vuex-persistedstate'

Vue.use(Vuex);

export const store = new Vuex.Store({
  state:{
    User:{},
    playlist:[],
    team:{},
    App:{
      Anime:[],
      login:false,
      noti:[]
    }
 },
  getters:{
    getteam:state=>{
      return state.team;
    },
    getProfile:state=> {
      return state.User;
    },
    loginStatus:state=>{
      return state.App.login;
    },
    getList:(state)=>{
      return state.playlist;
    },
    getcountry:(state)=>{
      return state.User.country;
    },
    getanime:(state)=>{
      return state.App.Anime;
    },
  },
  mutations:{
    anime: (state, anime)=>{
      state.App.Anime=anime;
    },
    toggleLogin: state=>{
      state.App.login=!state.App.login;
    },
    changeProfile:(state,profile)=>{
      state.User.user_id=profile.user_id,
      state.User.user_name=profile.user_name,
      state.User.user_avatar=profile.user_avatar,
      state.User.team_role=profile.team_role
    },
    ProfileImg: function(state,pro){
      state.User.pic=pro;
    },
    changeLoading: function(state){
      state.App.loading=!state.App.loading;
    },
    changeCountry: function(state,country){
      state.User.country=country;
    },
    updateNoti:function(state,noti){
      state.App.noti=noti;
    },
    changeTeam:function(state,team){
      state.team.team_id=team.team_id;
      state.team.team_name=team.team_name;
      state.team.team_img=team.team_img;
      state.team.team_description=team.team_description;
    },
    changeList:function(state,list){
      state.playlist=list;
    },
    changeRole:function(state){
      state.User.team_role="Admin";
    },
  },
actions:{
  setRole:function(context){
    context.commit('changeRole');
  },
  setList:function(context,list){
    context.commit('changeList',list);
  },
  setTeam:function(context,team){
    context.commit('changeTeam',team);
  },
  toggle_Login:context=>{
    setTimeout(function(){
      context.commit('toggleLogin');
    },200)
  },
  setAnime:function(context,anime){
    context.commit('anime',anime);
  },
  setProfile:function(context,pro){
    context.commit('changeProfile',pro);
  },
  toggle_Loading:function(context){
    context.commit('changeLoading');
  },
  setCountry:function(context,country){
    context.commit('changeCountry',country);
  },
  setNoti:function(context,noti){
    context.commit('updateNoti',noti);
  }
},
plugins: [PersistedState()]
})
